'use client';

import Image from 'next/image';
import Link from 'next/link';
import { Product } from 'lib/shopify/types';
import { getSellerIdForHandle } from 'lib/demo/listing-sellers';
import { getSellerById } from 'lib/demo/sellers';
import Price from '../price';

interface ClassifiedProductCardProps {
  product: Product;
}

export default function ClassifiedProductCard({ product }: ClassifiedProductCardProps) {
  const sellerId = getSellerIdForHandle(product.handle);
  const seller = getSellerById(sellerId);
  
  if (!seller) {
    return null;
  }

  return (
    <Link
      href={`/product/${product.handle}`}
      className="group relative flex h-full flex-col overflow-hidden rounded-lg border border-neutral-200 bg-white transition-shadow hover:shadow-lg dark:border-neutral-800 dark:bg-black"
      prefetch={true}
    >
      {/* Product Image */}
      <div className="relative aspect-square w-full overflow-hidden bg-neutral-100 dark:bg-neutral-900">
        {product.featuredImage?.url && (
          <Image
            src={product.featuredImage.url}
            alt={product.title}
            fill
            sizes="(min-width: 768px) 33vw, (min-width: 640px) 50vw, 100vw"
            className="object-cover transition-transform duration-300 group-hover:scale-105"
          />
        )}
      </div>

      {/* Product Info */}
      <div className="flex flex-1 flex-col p-4">
        {/* Title and Price */}
        <div className="mb-3">
          <h3 className="mb-1 line-clamp-2 text-sm font-semibold text-neutral-900 dark:text-white">
            {product.title}
          </h3>
          <Price
            className="text-base font-bold text-blue-600 dark:text-blue-400"
            amount={product.priceRange.maxVariantPrice.amount}
            currencyCode={product.priceRange.maxVariantPrice.currencyCode}
          />
        </div>

        {/* Seller Info */}
        <div className="mt-auto space-y-2 border-t border-neutral-200 pt-3 dark:border-neutral-800">
          <div className="flex items-center gap-2">
            {/* Seller Avatar */}
            <div className="relative h-10 w-10 flex-shrink-0 overflow-hidden rounded-full border-2 border-neutral-200 dark:border-neutral-700">
              <Image
                src={seller.avatar}
                alt={seller.name}
                fill
                sizes="40px"
                className="object-cover"
              />
            </div>

            {/* Seller Details */}
            <div className="min-w-0 flex-1">
              <div className="truncate text-sm font-semibold text-neutral-900 dark:text-white">
                {seller.name}
              </div>
              <div className="flex items-center gap-1.5 text-xs text-neutral-600 dark:text-neutral-400">
                <span className="flex items-center gap-0.5 font-medium text-yellow-500">
                  ‚òÖ {seller.rating}
                </span>
                <span className="text-neutral-500">({seller.reviews})</span>
                <span>¬∑</span>
                <span className="truncate font-medium">{seller.location}</span>
              </div>
            </div>
          </div>

          {/* Contact Info */}
          <div className="space-y-1.5 text-xs">
            {seller.phone && (
              <div className="flex items-center gap-2 text-neutral-700 dark:text-neutral-300">
                <span className="text-base">üìû</span>
                <a
                  href={`tel:${seller.phone}`}
                  onClick={(e) => e.stopPropagation()}
                  className="font-medium hover:text-blue-600 dark:hover:text-blue-400"
                >
                  {seller.phone}
                </a>
              </div>
            )}
            {seller.email && (
              <div className="flex items-center gap-2 text-neutral-700 dark:text-neutral-300">
                <span className="text-base">‚úâÔ∏è</span>
                <a
                  href={`mailto:${seller.email}`}
                  onClick={(e) => e.stopPropagation()}
                  className="truncate font-medium hover:text-blue-600 dark:hover:text-blue-400"
                >
                  {seller.email}
                </a>
              </div>
            )}
          </div>
        </div>
      </div>
    </Link>
  );
}

